
 h1 {
    font-family: $fontB;
    color: #9B59B6;
    font-size: 100%;
    font-weight: 700;
    
   
}
.bgSkills {
    background-color: #c4c4c488;
    margin-top: 3em;
    padding-top: 3em;
    padding-bottom: 4em;
}
.box 
{

  background:#ffffff;
  width: 150px; 
  height: 200px; 
  display:flex;
  justify-content:center;
  align-items:center;
  position:relative;
  flex-direction:column;
  border-radius: 5px;
  box-shadow: #000;
  border-radius: 1px black;
  transition: transform .2s;
  margin: 0 auto;
 
}
 .box:hover
{
 transform:scale(1.2);
} 
.box .percent 
{
    
  width:100px;
  height:100px;
  position:relative;
}
.box .percent svg
{
  width:150px;
  height:150px;
  position:relative;
}
.box .percent svg circle
{

  fill:none;
  stroke-width:8;
  stroke:#000;
  transform:translate(12px,15px);
  stroke-dasharray:314;
  stroke-dashoffset:0;
  stroke-linecap:round;
}
.box .percent .zero circle:nth-child(1)
{
  stroke-dashoffset:0;
  stroke:#d8d7d7;
}
.box .percent .zero circle:nth-child(2)
{
  stroke-dashoffset:calc(395 - (314 * 90) / 100);
  stroke:#9B59B6;

}
.box .percent .num
{
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  display:flex;
  justify-content:center;
  align-items:center;
  position:absolute;
  color:#111;
}
.box .percent .num h4
{
    
  font-size:1.2em;
  margin: 0;
}
.box .percent .num h2 span
{
  font-size:20px;
}
.box .text
{
  font-family: $fontB;
  padding: 10px 0 0;
  color:#000000;
  font-weight:700;
  letter-spacing:1px;
}

.skillFrontContainer {
    background-color: #ffffff;
    width: 60%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    margin-left: 4em;
    margin-top: 3em;
    border-radius: 2em;
    padding: 2em 0;
    transform: translateX(-100%);
    transition: transform 0.8s ease-in-out;
   
    
}
.skillAllContainer {
    background-color: #ffffff;
    width: 60%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    margin-left: 26em;
    margin-top: 3em;
    border-radius: 2em;
    padding: 2em 0;
    transform: translateX(100%);
    transition: transform 0.8s ease-in-out;
    right: 0;
    
   
    
}
.title {
    align-self: flex-start; 
    margin-left: 20%;
    margin-bottom: 2.5%;
    color: #9B59B6;
    font-size: 250%;
    font-weight: 700;
}

.boxes {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.skillFrontContainer.show {
    transform: translateX(0); 
   
}
.skillAllContainer.show {
    transform: translateX(0); 
   
}

