.h1Dev {
     color: rgb(87, 182, 107);;
      }


@media (max-width: 768px) {
        .skillFrontContainer, .skillAllContainer {
            width: 90%;
            margin: 1em auto;
            padding: 1em;
            transform: none;
            transition: none;
        }
    
        .title {
            margin-left: 0;
            font-size: 1.5rem;
        }
    
        .box {
            width: 120px;
            height: 160px;
            transition: none;
            margin: 1em 0;
        }
    
        .box:hover {
            transform: none;
        }
    
        .box .percent {
            width: 100px;
            height: 100px;
        }
    
        .box .percent svg {
            width: 100px;
            height: 100px;
        }
    
        .box .text {
          font-size: 1rem;
          padding-top: 0.5em; 
          text-align: center;
      }
    }